.promo {
    background-image: url(../../img/mars-kosmos-korabl.png);
    min-height: 550px;
    background-position: center;
}

.promo__content {
    min-height: 550px;
}

.text__title {
    width: 415px;
}

.text-gradient {
    background-image: linear-gradient(to right, #ffffff, #ff3f03);
    color: transparent;
    background-clip: text;
}

.img-blick1 {
    @include coner($top: 27px, $left: -229px);
}

.img-blick2 {
    @include coner($top: -4px, $left: -19px);
}

.img-figure {
    @include coner($top: 55px, $left: -42px);

    @include media-laptop {
        @include coner($top: 36px, $left: 116px);
    }
    @include media-mobile {
        @include coner($top: 55px, $left: -42px);
    }
}

.promo__btn {
    width: 330px;
}