@mixin font ($size: 1.6rem, $height: 2.2rem) {
    font-size: $size;
    font-style: normal;
    line-height: $height;
}


@mixin columns ($width, $wrap) {
    display: flex;
    flex-wrap: $wrap;
    justify-content: space-between;
    align-items: center;
}

@mixin coner($top, $left) {
    position: relative;
    top: $top;
    left: $left;
}

@mixin media-laptop {
    @media (max-width: $content-width) { @content; }
}

@mixin media-tablet {
    @media (max-width: $tablet-width) { @content; }
}
  
@mixin media-mobile {
    @media (max-width: $mobile-big-width) { @content; }
}

@mixin media-mobilesmall {
    @media (max-width: $mobile-small-width) { @content; }
}
