.header__wrapper {
    height: 100px;
    background-color: rgba(21,10,0,0.212);
    border: 1px solid rgba(152,147,147,0.212);
    background-blend-mode: lighten;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include media-tablet {
        padding-left: 24px;
    }
}

.header__logo-line  {
    display: flex;
    @include media-mobile {
        justify-content: center;
        flex-grow: 1;
        position: relative;
    }
}

.img-corner1 {
    @include coner($top: 47px, $left: -11px);
}

.img-corner2 {
    @include coner($top: -47px, $left: -18px);
}

.img-corner3 {
    @include coner($top: 47px, $left: 18px);
}

.img-corner4 {
    @include coner($top: -47px, $left: 11px);
}