html {
    font-size: 10px;
}

body {
    @extend %font-prim;
    @include font($size: 1.6rem);
}

h1 {
    @extend %font-prim;
    @include font($size: 5.8rem, $height: 5.4rem);
    text-transform: uppercase;
    color: $color-primary;
    @include media-mobile {
        @include font($size: 4.2rem, $height: 2.8rem);
    }
}

h2 {
    @include font($size: 4.6rem, $height: 5.2rem);
    color: $color-primary;
    @include media-mobile {
        @include font($height: 3.3rem, $size: 2.4rem);
    }
}

h3 {
    @include font($size: 2.6rem, $height: 3.2rem);
    color: $color-primary;
    @include media-mobile {
        @include font($height: 3.3rem, $size: 2.4rem);
    }
}