.navigation {
    display: flex;
    font-size: 1.6rem;
    line-height: 2.2rem;
}

.navigation__link {
    list-style: none;
    padding: 0 20px;
    cursor: pointer;
    text-decoration: none;
    @extend %transition;
    color: $color-primary;

    &:hover {
        color: $color-hover;
        text-decoration: underline;

    }
}

.header__navigation {
    @include media-tablet {
        display: none;
    }
}