.button {
    @extend %font-prim;
    @include font($size: 1.6rem, $height: 2.2rem);
    cursor: pointer;
    @extend %transition;
    width: 180px;
    height: 35px;
   
    background-color: rgba(22,21,29,0.5019607843137255);
    border: 1px solid rgba(152,147,147,0.212);
    color: $color-primary;
    border: 1px solid transparent;
    -moz-border-image: -moz-linear-gradient(top left, #021428, #1d334d);
    -webkit-border-image: -webkit-linear-gradient(top left, #021428, #1d334d);
    border-image: linear-gradient(to bottom right, #021428, #1d334d);
    border-image-slice: 1;
}

.button-link {
    cursor: pointer;
    //opacity: 0.031;
    width: 160px;
    height: 160px;
    background-color: rgba(22,21,29,0.031);
    color: $color-primary;
    margin-bottom: 10px;
    border: none;

    &:hover {
        border: 1px solid rgba(152,147,147,0.212);
    }
}

.text-btn {
    @extend %font-prim;
    font-style: normal;
    font-weight: normal;
    font-size: 1.6rem;
    line-height: 2.2rem;
}

